
.about {
  position: absolute;
  overflow-y: auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.about .close {
  position: absolute;
  right: 15px;
  font-size: 12px;
}
.about .large-close {
  width: 100%;
  height: 32px;
  display: block;
  text-align: center;
}
.about .content {
  color: #6285b7;
  position: absolute;
  background-color: #0c2850;
  width: 400px;
  padding: 14px;
  border: 1px solid #6285b7;
}
.about .content h3 {
  margin: 0;
  font-weight: normal;
}
.background {
  position: absolute;
  background-color: rgba(12,40,80,0.8);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (max-width: 800px) {
.about {
    -webkit-box-pack: initial;
        -ms-flex-pack: initial;
            justify-content: initial;
}
.about .content {
    width: 100%;
    border: none;
}
}


